<template>
  <div class="search">
    <!-- 搜索框 -->
    <label>
      <input
        type="text"
        placeholder="搜索歌曲、歌手、专辑"
        v-model.trim="searchtext"
        @keyup.enter="getSearchMultimatch();getSomeSongs()"
      />
      <span class="fa fa-search"></span>
      <span class="fa fa-close" @click="searchtext=null,searchMultimatch=null,someSongs=null"></span>
    </label>
    <!-- 热门搜索 -->
    <template v-if="!searchtext">
      <h5>热门搜索</h5>
      <ul class="searchhot">
        <li
          v-for="(item, index) in searchHot"
          :key="index"
          @click="searchtext=item.first,getSearchMultimatch();getSomeSongs()"
        >{{item.first}}</li>
      </ul>
    </template>
    <!-- 搜索建议 -->
    <template v-if="searchtext&&!searchMultimatch">
      <div class="suggest">
        <h5>搜索"{{searchtext}}"</h5>
        <ul>
          <li
            v-for="(item) in someSuggest"
            :key="item.keyword"
            @click="searchtext=item.keyword,getSearchMultimatch();getSomeSongs()"
          >
            <span class="fa fa-search"></span>
            {{item.keyword}}
          </li>
        </ul>
      </div>
    </template>
    <!-- 搜索建议 -->
    <div class="multimatch" v-if="searchMultimatch">
      <!-- 歌手 -->

      <template v-if="searchMultimatch.artist">
        <!-- <div
          class="artist"
          v-for="(item) in searchMultimatch.artist"
          :key="item.id"
          @click="$router.push('/artist?id='+item.id)"
        >-->
        <div
          class="artist"
          v-for="(item) in searchMultimatch.artist"
          :key="item.id"
          @click="artistClick(item.id)"
        >
          <img :src="item.picUrl" alt />
          <h5>
            <p>
              歌手：{{item.name}}
              <span v-for="(it, index) in item.alias" :key="index">({{it}})</span>
            </p>
            <p>
              <span>粉丝：{{translatePlayCount(item.fansSize)}}</span>
              <span>歌曲：{{item.musicSize}}</span>
              <span>专辑：{{item.albumSize}}</span>
            </p>
          </h5>
          <span class="fa fa-chevron-right"></span>
        </div>
      </template>
      <!-- mv -->

      <template v-if="searchMultimatch.mv">
        <div class="mv" v-for="(item) in searchMultimatch.mv" :key="item.id">
          <img :src="item.cover" alt />
          <h5>
            <p>
              MV:
              <span>{{item.name}}</span>
            </p>
            <p>{{item.artistName}}</p>
          </h5>
          <span class="fa fa-chevron-right"></span>
        </div>
      </template>
      <!-- 专辑 -->
      <template v-if="searchMultimatch.album">
        <div
          class="album"
          v-for="(item) in searchMultimatch.album"
          :key="item.id"
          @click="albumClick(item.id)"
        >
          <img :src="item.blurPicUrl" alt />
          <h5>
            <p>
              专辑：{{item.name}}
              <span v-for="(it, index) in item.alias" :key="index">({{it}})</span>
            </p>
            <p>{{item.artist.name}}</p>
          </h5>
          <span class="fa fa-chevron-right"></span>
        </div>
      </template>
    </div>
    <!-- 歌曲 -->
    <ul class="song" v-if="someSongs">
      <SongListItem
        v-for="(item,index) in someSongs"
        :key="item.id"
        :song-item="item"
        :index="index"
        @translate-currentid="$emit('translate-currentid',$event)"
      ></SongListItem>
    </ul>

    <img v-if="loading" src="../assets/timg.gif" alt />
  </div>
</template>

<script>
// import SearchSuggest from "@/components/SearchSuggest.vue";
import SongListItem from "@/components/SongListItem.vue";

export default {
  name: "HomeSearch",
  components: { SongListItem },
  data() {
    return {
      searchtext: null,
      searchHot: null,
      someSuggest: null,
      loading: false,
      searchMultimatch: null,
      someSongs: null
    };
  },
  methods: {
    // 热搜列表
    getSearchHot() {
      let searchHot = JSON.parse(window.localStorage.getItem("searchHot"));
      if (searchHot && searchHot.time > Date.now()) {
        this.searchHot = searchHot.data;
      } else {
        this.axios
          .get("http://musicapi.leanapp.cn/search/hot/detail")
          .then(response => {
            window.console.log(response);
            ({ hots: this.searchHot } = response.data.result);
            window.localStorage.setItem(
              "searchHot",
              JSON.stringify({
                time: Date.now() + 10 * 60 * 1000,
                data: this.searchHot
              })
            );
          });
      }
    },
    getSomeSuggest(word) {
      this.loading = true;
      this.axios
        .get(
          "http://music.kele8.cn/search/suggest?keywords= " +
            word +
            "&type=mobile"
        )
        .then(response => {
          window.console.log(response);
          ({ allMatch: this.someSuggest } = response.data.result);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getSearchMultimatch() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/search/multimatch", {
          params: {
            keywords: this.searchtext
          }
        })
        .then(response => {
          window.console.log(response);
          ({ result: this.searchMultimatch } = response.data);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getSomeSongs() {
      this.loading = true;
      this.axios
        .get("http://music.kele8.cn/search", {
          params: {
            keywords: this.searchtext,
            limit: 10,
            offset: 0
          }
        })
        .then(response => {
          window.console.log(response);
          ({ songs: this.someSongs } = response.data.result);
        })
        .finally(() => {
          this.loading = false;
        });
    },
    translatePlayCount(n) {
      if (n > 99999999) {
        n / 10000;
        return Number(n / 100000000).toFixed(2) + "亿";
      }
      if (n > 9999) {
        n / 10000;
        return Number(n / 10000).toFixed(1) + "万";
      }
    },
    artistClick(param) {
      // "$router.push('/artist?id='+item.id)"
      this.$router.push({ path: "artist", query: { id: param } });
    },
    albumClick(param) {
      this.$router.push({ path: "album", query: { id: param } });
    }
  },
  created() {
    this.getSearchHot();
  },
  watch: {
    searchtext(value) {
      if (value) {
        this.getSomeSuggest(value);
      }
    }
  }
};
</script>

<style lang='less' scoped>
.search {
  padding: 10px;
  margin: 20px 0;
  h5 {
    margin: 15px 0;
  }
  label {
    position: relative;
    margin: 15px 0;
    span {
      position: absolute;
      top: 3px;
      color: #9b9b9b;
      &.fa-close {
        right: 15px;
      }
      &.fa-search {
        left: 15px;
      }
    }
    input {
      width: 100%;
      height: 30px;
      // margin-left: 5px;
      // padding: 20px;
      padding-left: 40px;
      border-radius: 20px;
      border: 1px solid #cccccc;
      background-color: #ece9e9a8;
      // color: #cccccc;
      outline: none;
    }
  }
  .suggest {
    h5 {
      margin: 15px 0;
      color: skyblue;
    }
    li {
      margin: 15px 15px 15px 40px;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      border-bottom: 1px solid #e4e4e4;
      position: relative;
      span {
        position: absolute;
        top: 3px;
        left: -30px;
      }
    }
  }
  .searchhot {
    display: flex;
    flex-wrap: wrap;

    li {
      margin: 5px;
      padding: 5px 10px;
      border-radius: 15px;
      border: 1px solid #cccccc;
    }
  }
  .multimatch {
    .artist {
      display: flex;
      align-items: center;
      line-height: 50px;
      img {
        margin: 0 5px;
        width: 50px;
        height: 50px;
        border-radius: 50%;
      }
      span {
        width: 20px;
      }
      h5 {
        flex: 1;
        p {
          &:nth-child(1) {
            line-height: 30px;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
          }
          &:nth-child(2) {
            line-height: 20px;
            color: #cccccc;
            font-size: 12px;
          }
        }
      }
    }
    .mv {
      display: flex;
      align-items: center;
      line-height: 50px;
      img {
        margin: 0 5px;
        width: 90px;
        height: 50px;
      }
      span {
        width: 20px;
      }
      h5 {
        flex: 1;
        p {
          &:nth-child(1) {
            line-height: 30px;
          }
          &:nth-child(2) {
            line-height: 20px;
          }
          span {
            color: skyblue;
          }
        }
      }
    }
    .album {
      display: flex;
      align-items: center;
      line-height: 50px;
      img {
        margin: 0 5px;
        width: 50px;
        height: 50px;
      }
      span {
        width: 20px;
      }
      h5 {
        flex: 1;
        p {
          &:nth-child(1) {
            line-height: 30px;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            overflow: hidden;
          }
          &:nth-child(2) {
            line-height: 20px;
            color: skyblue;
            font-size: 12px;
          }
        }
      }
    }
  }
}
</style>