<template>
  <li class="song" @click="setCurrentSongId(songItem.id)">
    <div class="left" v-if="index !==undefined">{{index+1}}</div>
    <dl class="center">
      <dt>{{songItem.name}}</dt>
      <dd v-if="songItem.song">
        <span v-if=" songItem.song.hMusic && isShowQuality">SQ</span>
        {{ songItem.song.artists[0].name}}-{{ songItem.song.album.name}}
      </dd>
      <dd v-else-if="songItem.h">
        <span v-if="songItem.h  && isShowQuality">SQ</span>
        {{songItem.ar[0].name }}-{{songItem.al.name }}
      </dd>
      <dd v-else-if="songItem.artists">{{songItem.artists[0].name }}-{{songItem.album.name }}</dd>
      <dd v-else>
        <span v-if="isShowQuality">SQ</span>
        {{songItem.ar[0].name }}-{{songItem.al.name }}
      </dd>
    </dl>
    <div class="ce" @click="iflove = !iflove" :class="{ 'love': iflove }">
      <span class="fa fa-heart-o"></span>
    </div>
    <div class="right">
      <!-- stop-circle-o -->
      <span class="fa fa-play-circle-o"></span>
    </div>
  </li>
</template>

<script>
export default {
  props: ["songItem", "isShowQuality", "index"],
  data() {
    return {
      iflove: false
    };
  }, 
  methods: {
    setCurrentSongId(id) {
      window.console.log(id);
      this.$emit("translate-currentid", {
        songId: id,
        showFull: window.location.hash.match("search") ? true : false
      });
    }
    // changeLove() {
    //   return (this.iflove = !this.iflove);
    // }
  }
};
</script>

<style lang='less'>
li.song {
  // padding-left: 10px;
  margin: 8px 0;
  display: flex;
  dl.center {
    flex: 1;
    dt {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      line-height: 35px;
      font-size: 16px;
    }
    dd {
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      overflow: hidden;
      color: #a3a1a1;
      line-height: 25px;
      font-size: 14px;
      span {
        border: 1px solid #d43c33;
        color: #d43c33;
        font-size: 12px;
        line-height: 12px;
        transform: scale(0.2);
      }
    }
  }
  .left,
  .right,
  .ce {
    width: 50px;
    height: 60px;
    line-height: 60px;
    text-align: center;
  }
  .right,
  .ce {
    span {
      color: #969494;
      line-height: 60px;
      font-size: 22px;
    }
  }
  .ce {
    &.love {
      color: #d43c33;
    }
  }
}
</style>