<template>
  <div class="palyview">
    <audio :src="curentSongUrl" autoplay loop></audio>
    <template v-if="curentBar">
      <div class="play-bar">
        <div @click="isShowFull=true">
          <img :src="curentBar.al.picUrl" :class="{'paused':isPaused}" />
          <span class="bb fa fa-expand"></span>
        </div>
        <h3>{{curentBar.name}}</h3>
        <!-- 控制器 -->
        <span
          class="progress fa fa-play"
          @click.stop="pauseAudio"
          :class="{ 'fa-pause': !isPaused }"
        >
          <canvas width="40px" height="40px"></canvas>
        </span>
      </div>
      <template v-if="isShowFull">
        <div class="play-full">
          <div class="fubg" :style="{'background-image':`url(${curentBar.al.picUrl}`}"></div>
          <span class="proo fa fa-compress" @click="isShowFull=false,ifgeci=false"></span>
          <!-- <mark>1212122</mark> -->
          <div class="fu-bar" v-if="!ifgeci">
            <img class="toppic" src="../assets/needle-ip6.png" alt />
            <img
              class="t"
              :src="curentBar.al.picUrl"
              @click="ifgeci=!ifgeci"
              :class="{'paused':isPaused}"
            />
            <h3>{{curentBar.name}}</h3>
            <h3>{{curentBar.ar[0].name}}</h3>
          </div>
          <template v-if="ifgeci">
            <div class="geci" :class="{'scrolling':iftouch}" @click.stop="ifgeci=!ifgeci">
              <h2>{{curentBar.name}}</h2>
              <h3 class="geli">{{curentBar.ar[0].name}}</h3>
              <!-- ========================= -->
              <ul
                @scroll="scroll"
                @touchstart="touchStart"
                @touchmove="touchMove"
                @touchend="touchEnd"
              >
                <li v-for="(item, index) in geci" :key="index">{{item.text}}</li>
              </ul>
            </div>
          </template>
          <div class="kongzhiqi">
            <span class="fa fa-step-backward"></span>
            <span class="fa fa-play" @click="pauseAudio" :class="{ 'fa-pause': !isPaused }"></span>
            <span class="fa fa-step-forward"></span>
          </div>
        </div>
      </template>
    </template>
    <img v-if="loading" src="../assets/timg.gif" alt />
  </div>
</template>

<script>
export default {
  props: ["curentsongId", "showFull"],
  data() {
    return {
      curentBar: null,
      isPaused: false,
      currentTime: 0,
      geci: null,
      isShowFull: this.showFull || false,
      ifgeci: false,
      timer: true,
      currentIndex: 0,
      loading: false,

      iftouch: false
    };
  },
  computed: {
    curentSongUrl() {
      return (
        "https://music.163.com/song/media/outer/url?id=" +
        this.curentsongId +
        ".mp3"
      );
    }
  },

  methods: {
    getCurentBar() {
      this.loading = true;

      this.axios
        .get("http://music.kele8.cn/song/detail", {
          params: {
            ids: this.curentsongId
          }
        })
        .then(response => {
          window.console.log(response);
          this.curentBar = response.data.songs[0];
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getGeCi() {
      this.axios
        .get("http://music.kele8.cn/lyric", {
          params: {
            id: this.curentsongId
          }
        })
        .then(response => {
          window.console.log(response);
          this.geci = response.data.lrc.lyric;
          let arr = [];
          this.geci.split("\n").forEach((element, index) => {
            var t = element.slice(1, 10);
            arr[index] = {
              time: Number(t.slice(0, 2)) * 60 + Number(t.slice(3)),
              text: element.slice(11).trim()
            };
          });
          arr.pop();
          this.geci = arr;
        });
    },
    pauseAudio() {
      let audio = this.$el.querySelector("audio");
      if (this.isPaused) {
        audio.play();
      } else {
        audio.pause();
      }
      this.isPaused = audio.paused;
    },
    drawCircle() {
      let audio = this.$el.querySelector("audio");
      let canvas = this.$el.querySelector("canvas");
      let ctx = canvas.getContext("2d");

      let id = window.setInterval(() => {
        this.currentTime = audio.currentTime * 1000;

        ctx.clearRect(0, 0, 40, 40);
        //
        ctx.beginPath();
        ctx.arc(20, 20, 19, 0, 2 * Math.PI);
        ctx.closePath();
        ctx.strokeStyle = "#ccc";
        ctx.stroke();
        //
        ctx.beginPath();
        ctx.arc(
          20,
          20,
          19,
          -0.5 * Math.PI,
          2 * Math.PI * (this.currentTime / this.curentBar.dt) - 0.5 * Math.PI
        );
        ctx.strokeStyle = "red";
        ctx.stroke();
      }, 500);
      return id;
    },
    geciscroll() {
      // let audio = this.$el.querySelector("audio");
      let geciBox = document.querySelector(".geci ul");
      let geci = this.geci;

      let lis = document.querySelectorAll(".geci ul li");
      let currentTime = this.currentTime / 1000;

      // window.console.log(currentTime);

      // 查找当前歌词索引
      for (let i = 0; i < geci.length; i++) {
        if (geci[i].time >= currentTime) {
          // 如果可以当前歌曲时间 小于某个 歌词时间
          this.currentIndex = i;
          break;
        }
      }
      // 设置当前歌词高亮
      lis.forEach((element, index) => {
        // console.log(index, currentIndex)
        if (index == this.currentIndex) {
          if (index < 1) {
            lis[0].classList.add("active");
          } else if (index == geci.length - 1) {
            lis[index].classList.add("active");
          } else {
            lis[index - 1].classList.add("active");
          }
        } else {
          lis[index].classList.remove("active");
        }
      });

      // 判断当前歌词展示位置  如果大于高度的一半让其垂直居中
      let listop = lis[this.currentIndex].offsetTop;
      let boxtop = geciBox.offsetTop;

      if (listop - boxtop > 220) {
        geciBox.scrollTo({
          top: listop - boxtop - 220,
          behavior: "smooth"
        });
      }

      //
    },
    //歌词拖动
    scroll(event) {
      event.preventDefault();
    },
    touchStart() {
      this.timer = false;
    },
    touchMove() {
      let geciBox = document.querySelector(".geci ul");

      this.currentIndex = Math.round(geciBox.scrollTop / 20 + 1);
      window.console.log(this.currentIndex);
      this.iftouch = true;
    },
    touchEnd() {
      let audio = this.$el.querySelector("audio");

      this.timer = true;
      audio.currentTime = this.geci[this.currentIndex - 1].time;
      this.geci;
      this.iftouch = false;
    }
  },
  created() {
    this.getCurentBar();
    this.getGeCi();
  },
  watch: {
    curentsongId: function() {
      this.getCurentBar();
      this.isShowFull = this.showFull;
      this.getGeCi();
    },
    isPaused() {
      if (this.isPaused) {
        window.clearInterval(window.animateId);
      } else {
        window.clearInterval(this.animateId);
        window.animateId = this.drawCircle();
      }
    },
    curentBar() {
      this.$nextTick(function() {
        window.clearInterval(this.animateId);
        window.animateId = this.drawCircle();
      });
    },
    currentTime() {
      if (this.ifgeci && this.timer) {
        this.geciscroll();

        // this.aa();
      }
    }
  },
  updated() {}
};
</script>

<style lang='less'>
audio {
  position: fixed;
  bottom: 30vh;
  left: 0;
  width: 100%;
  height: 70px;
}
.palyview {
  position: fixed;
  bottom: 0;
  left: 0;
  background-color: aliceblue;
  width: 100vw;
  z-index: 10;
  .play-bar {
    display: flex;
    border: 1px solid #cccccc;
    padding: 10px;
    & > div {
      position: relative;
      img {
        width: 40px;
        height: 40px;
        border-radius: 50%;
        animation: myroll 10s linear infinite;
        &.paused {
          animation-play-state: paused;
        }
      }
      .bb {
        color: white;
        font-size: 25px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
      }
    }

    @keyframes myroll {
      from {
        transform: rotate(0);
      }
      to {
        transform: rotate(360deg);
      }
    }
    h3 {
      flex: 1;
      margin: 0 10px;
      line-height: 40px;
    }
    .progress {
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      color: #999999;
      position: relative;
      canvas {
        position: absolute;
        top: 0;
        left: 0;
      }
    }
    .proo {
      width: 40px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      font-size: 28px;
      color: #999999;
    }
  }
  .play-full {
    width: 100vw;
    height: 100vh;
    color: white;
    position: relative;
    &::after {
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.3);
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
    }
    span {
      color: #ffffff;
      // line-height: 30px;
      padding: 10px;
      font-size: 30px;
      position: relative;
      z-index: 99;
    }
    .fubg {
      position: absolute;
      bottom: 0;
      left: 0;
      background-position: center center;
      background-size: cover;
      filter: blur(10px);
      width: 100%;
      height: 100%;
      z-index: -1;
    }
    .fu-bar {
      margin-top: 30px;
      display: flex;
      flex-direction: column;
      align-items: center;
      img.toppic {
        width: 94px;
        position: absolute;
        top: 0;
        left: 50%;
        z-index: 9;
        transform: translateX(-15%);
        //
      }
      img.t {
        width: 65vw;
        height: 65vw;
        border-radius: 50%;
        border: 10vw solid #333;
        animation: myroll 5s linear infinite;
        box-shadow: 0 0 0 10px rgba(255, 255, 255, 0.3);
        &.paused {
          animation-play-state: paused;
        }
      }

      @keyframes myroll {
        from {
          transform: rotate(0);
        }
        to {
          transform: rotate(360deg);
        }
      }
      h3 {
        font-size: 26px;
        margin: 20px 10px;
      }
    }
    .kongzhiqi {
      width: 100%;
      padding-bottom: 10px;
      position: absolute;
      bottom: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: space-around;
      // background-color: rgba(0, 0, 0, );
      span {
        font-size: 40px;
      }
    }
  }
}
.geci {
  width: 100%;
  // overflow-y: scroll;
  position: relative;
  top: 0;
  left: 0;
  text-align: center;
  display: flex;
  align-items: center;
  flex-direction: column;
  height: 80vh;

  h3,
  h2 {
    font-size: 18px;
    line-height: 30px;
    &.geli {
      font-size: 16px;

      padding-bottom: 20px;
    }
  }

  &.scrolling {
    &::before {
      content: "";
      display: block;
      position: absolute;
      width: 100%;
      top: 40%;
      /* margin-top: 10px; */
      left: 0;
      border-top: 1px dashed rgb(195, 191, 191);
    }
  }

  ul {
    overflow-y: scroll;
    padding: 20vh 0;
  }

  li {
    padding: 0 50px;
    text-align: center;
    line-height: 25px;
    font-size: 14px;

    &.active {
      color: orange;
    }
  }
}
</style>