<template>
  <div class="recommend">
    <HomeTitle>推荐歌单</HomeTitle>
    <ul class="list">
      <SongListCard v-for="(item) in personalized" :key="item.id" :item="item" />
    </ul>
    <HomeTitle>最新音乐</HomeTitle>
    <ol>
      <SongListItem
        v-for="(item, index) in newsong"
        :key="item.id"
        :songItem="item"
        :is-show-quality="true"
        :index="index"
        @translate-currentid="$emit('translate-currentid',$event)"
      ></SongListItem>
    </ol>
  </div>
</template>

<script>
import HomeTitle from "@/components/HomeTitle.vue";
import SongListCard from "@/components/SongListCard.vue";
import SongListItem from "@/components/SongListItem.vue";

export default {
  name: "HomeRecommend",
  components: { HomeTitle, SongListCard, SongListItem },
  data: function() {
    return {
      personalized: [],
      newsong: []
    };
  },
  methods: {
    getPersonalized() {
      let personalized = JSON.parse(
        window.localStorage.getItem("personalized")
      );
      if (personalized && personalized.time > Date.now()) {
        this.personalized = personalized.data;
      } else {
        this.axios
          .get("http://musicapi.leanapp.cn/personalized?limit=6")
          .then(response => {
            window.console.log(response);
            ({ result: this.personalized } = response.data);
            window.localStorage.setItem(
              "personalized",
              JSON.stringify({
                time: Date.now() + 10 * 60 * 1000,
                data: this.personalized
              })
            );
          });
      }
    },
    getNewsong() {
      let newsong = JSON.parse(window.localStorage.getItem("newsong"));
      if (newsong && newsong.time > Date.now()) {
        this.newsong = newsong.data;
      } else {
        this.axios
          .get("http://musicapi.leanapp.cn/personalized/newsong")
          .then(response => {
            window.console.log(response);
            ({ result: this.newsong } = response.data);
            window.localStorage.setItem(
              "newsong",
              JSON.stringify({
                time: Date.now() + 10 * 60 * 1000,
                data: this.newsong
              })
            );
          });
      }
    }
  },
  created() {
    this.getPersonalized();
    this.getNewsong();
  }
};
</script>

<style lang="less">
.list {
  display: flex;
  flex-wrap: wrap;
  li.card {
    flex: 1 0 30%;
    margin: 0 0 5px 0;
    &:nth-child(3n + 2) {
      margin: 0 5px 5px 5px;
    }
  }
}
</style>