<template>
  <li v-if="item.picUrl" class="card" @click="handlerCardClick(item.id)">
    <div class="thumb">
      <img :src="item.picUrl" alt />

      <span class="aa">{{translatePlayCount(item.playCount)}}</span>
    </div>
    <div class="name">{{item.name}}</div>
  </li>
  <li v-else class="card bang" @click="handlerCardClick(item.id)">
    <div class="thumb">
      <img :src="item.coverImgUrl" alt />

      <span>{{item.updateFrequency}}</span>
    </div>
    <div class="name">{{item.name}}</div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object
  },
  methods: {
    translatePlayCount(n) { 
      if (n > 99999999) {
        n / 10000;
        return Number(n / 100000000).toFixed(2) + "亿";
      }
      if (n > 9999) {
        n / 10000;
        return Number(n / 10000).toFixed(1) + "万";
      }
    }, 
    handlerCardClick(param) {
      // window.console.log(param);
      this.$router.push({ path: "playlist", query: { id: param } });
    }
  }
};
</script>

<style lang="less">
li.bang {
  
  .thumb {
    span {
      bottom: 2vw;
      left: 2vw;
      position: absolute;

      color: white;
      font-size: 10px;
    }
  }
}
.thumb {
  position: relative;
  img {
    vertical-align: middle;
  }
  span.aa {
    position: absolute;
    top: 2vw;
    right: 2vw;
    color: white;
    font-size: 10px;
  }
}
.name {
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  font-size: 14px;
  overflow: hidden;
  margin: 5px 10px;
}
</style>