<template>
  <div class="hot" v-if="hotsong">
    <HotPic :song-updatetime="hotsong"></HotPic>
    <HomeTitle>最热音乐</HomeTitle>

    <!-- 全部榜单 -->
    <!-- <ul class="list">
      <SongListCard v-for="(item,index) in bangSome" :key="item.id" :item="item" :index="index" />
    </ul>-->
    <!-- 热歌 -->
    <ol v-if="hotsong.tracks">
      <SongListItem
        v-for="(item, index) in hotsong.tracks"
        :key="item.id"
        :song-item="item"
        :is-show-quality="true"
        :index="index"
        @translate-currentid="$emit
          ('translate-currentid',$event)"
      ></SongListItem>
    </ol>

    <img v-if="loading" src="../assets/timg.gif" alt />
  </div>
</template>

<script>
import SongListItem from "@/components/SongListItem.vue";
// import SongListCard from "@/components/SongListCard.vue";
import HomeTitle from "@/components/HomeTitle.vue";

import HotPic from "@/components/HotPic.vue";

export default {
  name: "HomeHot",
  components: { SongListItem, HotPic, HomeTitle },
  // SongListItem,HotPic, HomeTitle SongListCard
  data() {
    return {
      loading: false,
      hotsong: null,
      bangSome: null
    };
  },
  methods: {
    getBangSome() {
      this.axios.get("http://music.kele8.cn/toplist").then(response => {
        window.console.log(response, "xxx");
        ({ list: this.bangSome } = response.data);
      });
    },
    getHotsong() {
      this.loading = true;
      let hotsong = JSON.parse(window.localStorage.getItem("hotsong"));
      if (hotsong && hotsong.time > Date.now()) {
        this.hotsong = hotsong.data;
        this.loading = false;
      } else {
        this.axios
          .get("http://musicapi.leanapp.cn/top/list?idx=1")
          .then(response => {
            window.console.log(response);
            ({ playlist: this.hotsong } = response.data);
            window.localStorage.setItem(
              "hotsong",
              JSON.stringify({
                time: Date.now() + 10 * 60 * 1000,
                data: this.hotsong
              })
            );
          })
          .finally(() => {
            this.loading = false;
          });
      }

      // this.axios
      //   .get("https://musicapi.leanapp.cn/top/list?idx=1")
      //   .then(response => {
      //     window.console.log(response);
      //     window.console.log("xxx");
      //     ({ playlist: this.hotsong } = response.data);
      //   });
    }
  },
  created() {
    this.getHotsong();
    // this.getBangSome();
  }
};
</script>

<style>
.hot {
  position: relative;
  overflow: hidden;
}
</style>